@import url("https://fonts.googleapis.com/css2?family=Monoton&family=Quicksand:wght@500;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 2.4rem;
  font-family: sans-serif;
  color: #0d0805;
  font-family: "Quicksand";
  font-weight: 500;
}

.app {
  width: 100%;
  height: 100vh;
  display: grid;
  grid-template-rows: auto auto 1fr auto;
}

h1 {
  color: azure;
  text-align: center;
  background-color: #4310a1;
  font-family: "Roboto", sans-serif;
  font-size: 4.5rem;
  text-transform: uppercase;
  font-weight: 400;
  word-spacing: 10px;
  letter-spacing: -5px;
  padding: 2.4rem 0;
}

.add-form {
  background-color: #662bd3;
  padding: 2.8rem 0;
  display: flex;
  flex-direction: column;

  align-items: center;
  /* justify-content: space-evenly; */
  gap: 0.8rem;
}

.qty {
  flex-shrink: 1;
}

h3 {
  margin-right: 1.6rem;
  font-size: 2.4rem;
}

button,
select,
input {
  background-color: #ccc1e2;
  color: #5a3e2b;
  font-family: inherit;
  border: none;
  border-radius: 10rem;
  padding: 1.2rem 3.2rem;
  font-weight: 700;
  font-size: 1.8rem;
  cursor: pointer;
}

.add-form button {
  text-transform: uppercase;
  background-color: #9a30be;
  color: #ffffff;
}

.list {
  background-color: #efe4fa;
  color: #220b3f;
  padding: 4rem 0;

  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 3.2rem;
  align-items: center;
  overflow: auto;
  height: 500px;
}

.actions button,
.list select {
  text-transform: uppercase;
  padding: 0.8rem 2.4rem;
  font-size: 1.4rem;
  font-weight: 700;
  margin: 0 0.8rem;
}

.list ul {
  list-style: none;
  width: 80%;
  overflow: scroll;

  /* display: grid; */
  /* grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); */
  gap: 1.2rem;
  justify-content: center;
  align-content: start;
}

.list li {
  display: flex;
  align-items: center;
  gap: 1.2rem;
  /* word-spacing: 200px; */
}

input[type="checkbox"] {
  height: 2rem;
  width: 2rem;
  accent-color: #e5771f;
}

.list li button {
  cursor: pointer;
  background: none;
  border: none;
  font-size: 1.8rem;
  padding: 0.8rem;
  transform: translateY(2px);
}

.stats {
  background-color: #76c7ad;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
}

.footer {
  background-color: #76c7ad;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;

  /* // */

  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}

.red {
  background-color: #a8155f;
  text-align: center;
  font-weight: 700;
  padding: 3.2rem 0;
  color: white;

  /* // */

  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.rred {
  color: red;
}

.ll {
  display: flex;
  align-items: center;
}

.listahan {
  flex: 1;
  display: flex;
  justify-content: space-between;
}

/* In your project's CSS file */
.toast {
  /* Customize individual toast styles */
  position: relative;
}

.toast-title {
  /* Customize toast title styles */
  position: absolute;
  top: 0;
}

.toast-message {
  /* Customize toast message styles */
  margin-right: auto;
}

.menu {
  padding: 2.8rem 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 1.2rem;
  align-items: center;
}

.menu .isaw {
  background-image: url("isaw.png");
  background-size: contain;
}
.menu .dugo {
  background-image: url("dugo.png");
  background-size: contain;
}
.menu .adidas {
  background-image: url("adidas.png");
  background-size: contain;
}

.menu .laman {
  background-image: url("bbq.png");
  background-size: contain;
}

.menu .hotdog {
  background-image: url("hotdog.png");
  background-size: contain;
}
.menu .maskara {
  background-image: url("maskara.png");
  background-size: contain;
}

.menu button {
  height: 100px;
  width: 100px;
}
